
.mobile {
  .wallet-panel {
    min-width: 3.75rem;
    width: 100%;
    height: 0.58rem;
    background: #000000;
    box-shadow: 0px -3px 4px rgba(0, 0, 0, 0.2);
    position: fixed;
    display: flex;
    align-items: center;
    justify-content: center;
    bottom: 0;
    z-index: 1;

    .logo-text ,.network-text-logo{
      display: none;
    }

    .connect {
      margin-left: 1.4rem;
      font-family: 'korolev';

      button {
        font-family: 'korolev';
        font-weight: 300;
        white-space: nowrap;
      }

    }


    .premining {
      width: 2rem;
      height: 0.4rem;
      background: #246CAD;
      color: #fff;
      text-align: center;
      line-height: 0.4rem;
      font-family: "Korolev";
      font-size: 0.16rem;
    }
  }
  .wallet-panel::before {
    content: '';
    line-height: 0.4rem;
  }
}
.index,.team{
  .wallet-panel{
    background-color: #25282b;
    box-shadow:0px 0px 0px rgb(0 0 0 / 20%);
    position: initial;
    margin-top: -0.59rem;
    .connect{
      display: none;
    }
  }
}
.team,.index{
  .wallet-panel{
    display: none;
  }
}